.menu {
  height: 100vh;
  background-color: $menu-bg-color;
  color: $secondary-color;
  font-weight: 600;
  text-transform: uppercase;
  overflow: auto;

  &__top {
    position: sticky;
    top: 0;
    z-index: 99;
    margin-bottom: 24px;
    background-color: $menu-bg-color;
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;

    color: inherit;
    line-height: 27px;
  }

  &__book-a-ride {
    display: block;
    width: fit-content;
    padding-bottom: 7px;
    border-bottom: 1px solid $secondary-color;

    color: inherit;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
  }
}
